<template>
  <div>
    <b-row>
      <b-col md="4">
        <b-card>
          <div
            class="card-title d-flex justify-content-between align-items-center"
          >
            Inbox List
            <div>
              <b-button
                variant="outline-primary"
                size="sm"
                @click="openModal('msgFilter')"
                class="mr-1"
              >
                <feather-icon icon="FilterIcon" size="16" />
                Filter
              </b-button>
              <b-button variant="primary" size="sm" v-b-modal.msgFilter>
                <feather-icon icon="PlusIcon" size="16" />
                New
              </b-button>
            </div>
          </div>
          <div class="full-heigt">
            <div
              class="msg d-flex align-items-center"
              v-for="msg in messages"
              :id="'msg' + msg.id"
              :key="msg.id"
              @click="detail(msg)"
            >
              <div>
                <b-img
                  src="https://via.placeholder.com/40"
                  class="rounded-circle"
                  alt="profile"
                ></b-img>
              </div>
              <div
                class="d-flex justify-content-between align-items-center ml-1 w-100"
              >
                <div>
                  <h6>{{ msg.sender }}</h6>
                  <span>{{ msg.subject }}</span>
                </div>
                <div>
                  <span>{{ msg.sendDate }}</span>
                </div>
              </div>
            </div>
          </div>
        </b-card>
      </b-col>
      <b-col md="8">
        <b-card title="Inbox Detail" style="padding-bottom: 0.75rem">
          <div class="full-heigt" v-if="detailInbox == false">
            <div class="d-flex justify-content-center align-items-center h-100">
              <span class="text-center">
                <feather-icon
                  size="80"
                  icon="MessageSquareIcon"
                  class="mr-50"
                />
                <br />
                <h2>Select an Inbox!</h2>
              </span>
            </div>
          </div>
          <div class="full-heigt" v-else>
            <table class="semi-bold">
              <tr>
                <td width="60">From</td>
                <td>: {{ detailInboxData.sender }}</td>
              </tr>
              <tr>
                <td>Type</td>
                <td>: {{ detailInboxData.subject }}</td>
              </tr>
              <tr>
                <td>Date</td>
                <td>: {{ detailInboxData.sendDate }}</td>
              </tr>
            </table>
            <div
              v-if="
                detailInboxData.detail.reminder_type.name == 'Goal Settings'
              "
            >
              <p class="mt-1">
                A reminder for setting goals for the period
                <b>{{ detailInboxData.detail.start_period }}</b> until the
                period <b>{{ detailInboxData.detail.end_period }}</b> has been
                sent to you. <br />
                Please fill it in immediately.
              </p>

              {{ detailInboxData.detail.description }}

              <div class="text-right mt-3">
                <b-link
                  :to="{
                    name: 'performance/goal',
                    params: { id: detailInboxData.detail.id },
                  }"
                >
                  <b-button variant="primary" size="sm">
                    <feather-icon icon="ArrowRightIcon" />
                    Open
                  </b-button>
                </b-link>
              </div>
            </div>
            <div
              v-if="
                detailInboxData.detail.reminder_type.name ==
                'Midyear Evaluation'
              "
            >
              <p class="mt-1">
                A reminder for midyear evaluation for the period
                <b>{{ detailInboxData.detail.start_period }}</b> until the
                period <b>{{ detailInboxData.detail.end_period }}</b> has been
                sent to you. <br />
                Please fill it in immediately.
              </p>
              {{ detailInboxData.detail.description }}

              <div class="text-center mt-3">
                <b-button variant="primary" size="sm">Open Page</b-button>
              </div>
            </div>
            <div
              v-if="detailInboxData.detail.reminder_type.name == 'Final Review'"
            >
              <p class="mt-1">
                A reminder for final review for the period
                <b>{{ detailInboxData.detail.start_period }}</b> until the
                period <b>{{ detailInboxData.detail.end_period }}</b> has been
                sent to you. <br />
                Please fill it in immediately.
              </p>
              {{ detailInboxData.detail.description }}

              <div class="text-center mt-3">
                <b-button variant="primary" size="sm">Open Page</b-button>
              </div>
            </div>
          </div>
        </b-card>
      </b-col>
    </b-row>

    <b-modal id="msgFilter" title="Filter Inbox" size="md" hide-footer>
      <form @submit.prevent>
        <b-form-group label="Sender">
          <b-form-select>
            <b-form-select-option value="1">Type 1</b-form-select-option>
          </b-form-select>
        </b-form-group>
        <b-form-group label="Type">
          <b-form-select>
            <b-form-select-option value="1">Type 1</b-form-select-option>
          </b-form-select>
        </b-form-group>
        <b-form-group label="Date">
          <b-form-input type="date" placeholder="Enter Date"></b-form-input>
        </b-form-group>
        <div class="text-right">
          <b-button type="submit" variant="primary">Submit</b-button>
        </div>
      </form>
    </b-modal>
  </div>
</template>

<style>
.full-heigt {
  height: 57vh;
  overflow-y: auto;
}

.msg {
  border-bottom: 1px solid #e0e0e0;
  padding: 10px 10px;
  cursor: pointer;
}

.msg:hover {
  background-color: #f5f5f5;
}
</style>

<script>
import ToastificationContent from "@core/components/toastification/ToastificationContent.vue";
import moment from "moment";

export default {
  components: {
    ToastificationContent,
  },

  data() {
    return {
      detailInbox: false,
      detailInboxData: {},

      messages: [],
    };
  },

  mounted() {
    this.callApi({
      url: "/reminders/fetch",
      method: "GET",
      success: (res) => {
        res.result.data.map((item) => {
          this.messages.push({
            id: item.id,
            sender: item.created_by.name,
            subject: item.subject,
            message: item.message,
            sendDate: moment(item.date_added).format("DD MMMM YYYY"),
            detail: item,
          });
        });
      },
    });
  },

  methods: {
    openModal(modal) {
      this.$root.$emit("bv::show::modal", modal);
    },

    detail(msg) {
      this.detailInbox = true;
      this.detailInboxData = msg;
    },
  },
};
</script>